import React from "react";
import { between } from "utils";

import { snowFlakeList } from "./snowFlakeList";
import css from "../style.module.scss";

export const renderSnow = (count: number) => {
  const wrap = 100 / count;
  const arr = [];
  for (let i = 0; i < count; i++) {
    const size = `${10 + Math.floor(Math.random() * 40)}px`;
    const wrapperStyle = {
      animationDelay: `-${i % 15}s`,
      animationDuration: `${between(10, 15) + Math.floor(Math.random() * 10)}s`,
      left: `${wrap * i * 2}%`,
    };

    const unitStyle = {
      animationDelay: `${i % 5}s`,
      animationDuration: `${between(4, 7) + Math.random()}s`,
      fontSize: `calc(${size} - 2px)`,
      width: size,
    };

    const snowID = Math.round(between(0, 3));
    const Snow = snowFlakeList[snowID];

    arr.push(
      <span
        key={`@snowKey_${i}_${snowID}`}
        className={css.snowflakeWrapper}
        style={wrapperStyle}>
        <span className={css.snowflakeUnit} style={unitStyle}>
          <Snow />
        </span>
      </span>,
    );
  }
  return arr;
};
