import React from "react";

import { PopUpRoot } from "containers/PopUps";

import { renderSnow } from "./methods/renderSnow";
import css from "./style.module.scss";

const Snows = React.memo(() => <>{renderSnow(40)}</>); //memo -- because when rerendering, the animation restarts

const Snowflakes = () => {
  return (
    <PopUpRoot className={css.popUpWrapper}>
      <div className={css.root}>
        <Snows />
      </div>
    </PopUpRoot>
  );
};

export default Snowflakes;
